/*  Content with Buttons
*
*  Version:
*    1.0.0 - 2022/01/11
*/

@import '../base.scss';

.c-content-buttons {

  &__inner {
    @include t-gutters(true);
    padding-top: 3.75rem;
    padding-bottom: 5rem;
    text-align: center;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 1.5rem;
  }

  &__btn-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__btn-container {
    &:not(:last-of-type) {
      margin-right: 0.6875rem;
    }

    & .o-btn {
      min-width: 8.875rem;
    }
  }

  @include t-media-query($s-medium-up) {
    &__title {
      margin-bottom: 3rem;
    }

    &__btn-container {
      &:not(:last-of-type) {
        margin-right: 1.25rem;
      }

      & .o-btn {
        width: 10.8125rem;
      }
    }
  }
}